import React from "react";
import { Checkbox } from "@salesforce/design-system-react";

export default class GRAXEnvSettingSwitch extends React.PureComponent {
  render() {
    return (
      <div className="slds-form-element">
        <label className="slds-form-element__label" htmlFor={this.props.id}>
          {this.props.label}
        </label>
        {this.props.helpTextTooltip}
        <div className="slds-form-element__control">
          <Checkbox
            key={this.props.id}
            assistiveText={{
              label: this.props.label
            }}
            id={this.props.id}
            /*labels={{
                                    label
                                }}*/
            variant="toggle"
            defaultChecked={this.props.defaultValue}
            onChange={e => {
              e.persist();

              this.props.onChange(e.target.checked);
            }}
          />
        </div>
      </div>
    );
  }
}
